import { cn } from '../utils';

export class Label extends HTMLElement {
    connectedCallback() {
        // Create a new label element
        const labelElement = document.createElement('label');

        const content = this.textContent;

        // Set the class and content for the label
        labelElement.className = this.className;
        labelElement.textContent = content;

        // Add CSS to prevent text selection
        labelElement.style.userSelect = 'none';

        // Transfer all attributes from the custom element to the label
        for (const attr of this.attributes) {
            if (attr.name !== 'class') {
                labelElement.setAttribute(attr.name, attr.value);
            }
        }

        // Still thinking about how to handle this :3
        // This works for now
        this.replaceWith(labelElement);
    }
}